<template>
  <div
    class="Carousel"
    @mouseenter="mouseEnterEvent"
    @mouseleave="mouseLeaveEvent"
    :style="{ width: containerWidth, height: containerHeight }"
    :class="{ leftToRight: leftToRight }"
  >
    <slot></slot>

    <div
      v-show="isDirection && showDirection"
      class="direction-container"
    >
      <Direction
        @prevHandleClick="prevHandleClick"
        dir="prev"
      />
      <Direction
        @nextHandleClick="nextHandleClick"
        dir="next"
      />
    </div>

    <Indicator
      v-show="showIndicator"
      @before-moving="beforeEmit"
      @after-moving="afterEmit"
      @dicator-click="DicatorClick"
    />
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onMounted,
  onBeforeUnmount,
  provide,
} from "vue";
import Indicator from "./indicator.vue";
import Direction from "./direction.vue";

export default {
  name: "Carousel",
  emits: ["before-moving", "after-moving"],
  props: {
    containerWidth: { // 
      type: String,
      default: "100%",
    },
    containerHeight: { // 
      type: String,
      default: "100%",
    },
    duration: { // 
      type: Number,
      default: 3000,
    },
    initIndex: { //   index
      type: Number,
      default: 0,
    },
    autoplay: { // 
      type: Boolean,
      default: true,
    },
    direction: { //  
      type: Boolean,
      default: true,
    },
    directionMode: { //  
      type: String,
      default: "always", // always || hover
    },
    directionColor: { //  
      type: String,
      default: "white",
    },
    directionSize: { //  
      type: Number,
      default: 25,
    },
    indicator: { //  
      type: Boolean,
      default: true,
    },
    indicatorMode: { //  
      type: String,
      default: "always", // always || hover
    },
    indicatorColor: { //  
      type: String,
      default: "rgb(255 255 255 / 50%)",
    },
    indicatorActiveColor: { //  
      type: String,
      default: "#FFFFFF",
    },
  },
  components: {
    Indicator,
    Direction,
  },
  setup(props, { slots, emit }) {
    const state = reactive({
      CAROUSEL_ITEM_LEN: slots.default()[0].children.length,
      propsStaging: props, //  props 
      currentIndex: props.initIndex, // 
      showDirection: true, //  
      showIndicator: true, //  
      leftToRight: false, // 
    });

    //  currentIndex  provide 
    //  inject  watch 
    provide('carouselCtxState', state);

    let timer = null;
    const isDirection = props.direction;
    const {
      directionMode,
      indicatorMode,
      duration,
    } = toRefs(props);

    const autoplayFunc = () => {
      //  autoplay 
      if (props.autoplay) {
        timer = setInterval(() => {
          start("next");
        }, duration.value);
      }
    };

    //#region 
    onMounted(() => {
      autoplayFunc();
      //  hover  
      if (directionMode.value === 'hover') {
        state.showDirection = false;
      }
      //  hover  
      if (indicatorMode.value === 'hover') {
        state.showIndicator = false;
      }
    });

    onBeforeUnmount(() => {
      clearTimer();
    });
    //#endregion

    const beforeEmit = (data) => {
      emit("before-moving", data);
    };
    const afterEmit = (data) => {
      emit("after-moving", data);
    };

    const changeLeftTranslate = () => {
      state.leftToRight = true;
      setTimeout(() => {
        state.leftToRight = false;
      }, 500);
    }

    const start = (direction) => {
      beforeEmit({ index: state.currentIndex, direction });
      switch (direction) {
        case "prev":
          changeLeftTranslate();
          state.currentIndex -= 1;
          if (state.currentIndex === -1) {
            state.currentIndex = state.CAROUSEL_ITEM_LEN - 1;
          }
          break;
        case "next":
          state.currentIndex += 1;
          if (state.currentIndex === state.CAROUSEL_ITEM_LEN) {
            state.currentIndex = 0;
          }
          break;
      }
      afterEmit({ index: state.currentIndex, direction });
    };

    const DicatorClick = (idx) => {
      //    
      if (idx < state.currentIndex) {
        changeLeftTranslate();
      }
      state.currentIndex = idx;
    };

    const mouseEnterEvent = () => {
      clearTimer();
      //  hover  
      if (directionMode.value === 'hover') {
        state.showDirection = true;
      }
      //  hover  
      if (indicatorMode.value === 'hover') {
        state.showIndicator = true;
      }
    };

    const mouseLeaveEvent = () => {
      autoplayFunc();
      //  hover  
      if (directionMode.value === 'hover') {
        state.showDirection = false;
      }
      //  hover  
      if (indicatorMode.value === 'hover') {
        state.showIndicator = false;
      }
    };

    // 
    const prevHandleClick = () => {
      start("prev");
    };
    // 
    const nextHandleClick = () => {
      start("next");
    };

    function clearTimer() {
      clearInterval(timer);
      timer = null;
    }

    return {
      ...toRefs(state),
      DicatorClick,
      mouseEnterEvent,
      mouseLeaveEvent,
      prevHandleClick,
      nextHandleClick,
      isDirection,
      beforeEmit,
      afterEmit,
    };
  },
};
</script>

<style scoped>
.Carousel {
  position: relative;
  overflow: hidden;
}
.direction-container {
  z-index: 2;
}
/*  */
.Carousel >>> .v-enter-active,
.Carousel >>> .v-leave-active {
  transition: transform 0.5s linear;
}
/*  */
.Carousel >>> .v-enter-active {
  transform: translateX(100%);
}
.Carousel >>> .v-enter-to {
  transform: translateX(0);
}
.Carousel >>> .v-leave-active {
  transform: translateX(0);
}
.Carousel >>> .v-leave-to {
  transform: translateX(-100%);
}
/*  */
.leftToRight >>> .v-enter-active {
  transform: translateX(-100%) !important;
}
.leftToRight >>> .v-enter-to {
  transform: translateX(0) !important;
}
.leftToRight >>> .v-leave-active {
  transform: translateX(0) !important;
}
.leftToRight >>> .v-leave-to {
  transform: translateX(100%) !important;
}
</style>
